html,
body,
#root {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    height: 100vh;
}

input,
textarea {
    font-family: Arial
}

.login {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: url('./img/login_background.svg');
}

.login-form {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.login-form-logo img {
    width: 15vw;
}

.login-form h2 {
    font-size: 2vw;
}

.login-form input {
    max-width: 100%;
    border-top: none;
    border-left: none;
    border-right: none;
    height: 3vw;
    line-height: 3vw;
    font-size: 3vw;
    border-bottom: solid 2px #b3b2ca;
    transition: all .23s ease-in;
    background: #F4F4F9
}

.login-form input:focus {
    border-bottom: solid 2px #5d5d8a;
    outline: none
}

.login-form .error {
    text-align: center;
    margin: 5px 0;
    padding: 5px 10px;
    color: #c92c43
}

footer {
    position: absolute;
    bottom: 0
}

.container {
    color: #cac8ee;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    height: 100vh;
    width: 100%
}

#side-bar {
    box-sizing: border-box;
    width: 30%;
    height: 100vh;
    display: grid;
    justify-content: stretch;
    grid-template-rows: 8vh 5vh 1fr 8vh;
    transition: width 0.7s cubic-bezier(0.075, 0.82, 0.165, 1);
    -moz-transition: width 0.7s cubic-bezier(0.075, 0.82, 0.165, 1);
    /* Firefox 4 */
    -webkit-transition: width 0.7s cubic-bezier(0.075, 0.82, 0.165, 1);
    /* Safari and Chrome */
    -o-transition: width 0.7s cubic-bezier(0.075, 0.82, 0.165, 1);
    /* Opera */
    -ms-transition: width 0.7s cubic-bezier(0.075, 0.82, 0.165, 1);
    /* Explorer 10 */
}



#side-bar .heading {
    box-sizing: border-box;
    max-height: 65px;
    padding: 18px 16px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background: #282C33
}

#side-bar .side-bar-select {
    display: flex;
    justify-content: space-around;
    align-items: center
}

#side-bar .side-bar-select__option {
    background: #31363F;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer
}

#side-bar .side-bar-select__option.active {
    background: #282C33
}

#side-bar .search {
    background: #282C33;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-width: 1px 0;
    border-style: solid;
    border-color: black;
    padding-left: 15px;
    padding-right: 20px
}

#side-bar .search .search-icon .menu-btn {
    margin-right: 15px;
    cursor: pointer
}

#side-bar .search input {
    width: 100%;
    background: #282C33;
    flex-grow: 1;
    box-sizing: border-box;
    border: none;
    color: #cac8ee
}

#side-bar .search input:focus {
    outline: none
}

#side-bar .search input::placeholder {
    color: #cac8ee;
    opacity: .6
}

#side-bar .search .plus {
    display: inline-block;
    position: relative;
    cursor: pointer;
    width: 13px;
    height: 13px
}

#side-bar .search .plus::after,
#side-bar .search .plus::before {
    content: '';
    position: absolute;
    background: #b3b2ca
}

#side-bar .search .plus::after {
    width: 16px;
    height: 2px;
    top: 5px
}

#side-bar .search .plus::before {
    width: 2px;
    height: 16px;
    top: -2px;
    left: 7px
}

#side-bar .users {
    overflow-y: scroll;
    background: #31363F;
    flex-grow: 1
}

#side-bar .users::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #cac8ee
}

#side-bar .users::-webkit-scrollbar {
    width: 5px;
    background-color: green
}

#side-bar .users::-webkit-scrollbar-thumb {
    background-color: #484d79
}

#side-bar .users .user {
    align-items: center;
    display: flex;
    height: 66px;
    justify-content: flex-start;
    padding: 18px 16px
}

#side-bar .users .user.active,
#side-bar .users .user:hover {
    background: #282C33
}

#side-bar .users .user:hover:not(.active) {
    cursor: pointer
}

#side-bar .users .user .user-info {
    margin-left: 15px;
    flex-grow: 1;
    width: 100%
}

#side-bar .users .user .user-info .last-message {
    font-size: 12px;
    opacity: .56
}

#side-bar .users .user .new-message {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center
}

#side-bar .current-user {
    align-items: center;
    background: #3C424C;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 16px;
    padding-right: 16px;
    max-height: 10vh;
}

#side-bar .current-user .logout {
    align-items: center;
    cursor: pointer;
    display: flex;
    justify-content: center;
    font-size: 2em
}


.chat-header {
    background: #3C424C;
    box-shadow: 0px 6px 5px -2px rgba(225, 225, 225, 0.7);
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 18px 16px;
}

.chat-header .user-info {
    align-items: center;
    display: flex
}

.chat-header .user-info .user-name {
    margin-right: 10px
}

.chat-header .user-info .status {
    align-items: center;
    display: flex
}

.chat-header .options {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-around;
    height: 100vh;
    width: 15%
}

.options img {
    width: 3vw
}

.chat-header .options svg {
    cursor: pointer
}

.chat-room-container {
    height: 100vh;
    width: 100vw;
}

@media screen and (max-width: 510px) {
    #side-bar {

        width: 100vw;
    }

    .chat-room-container {
        width: 100%
    }

    .options img {
        width: 10vw
    }
}

.chat-room {
    display: grid;
    grid-template: 8vh 1fr 8vh / 100%;
    height: 100vh;
    width: 100%
}

.chat-room.choose {
    align-items: center;
    justify-content: center;
    font-size: 2em
}

.thread-container {
    display: grid;
    flex-grow: 1;
    overflow-y: scroll;
    position: relative
}

.thread-container::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #cac8ee
}

.thread-container::-webkit-scrollbar {
    width: 5px;
    background-color: green
}

.thread-container::-webkit-scrollbar-thumb {
    background-color: #484d79
}

.thread-container .thread {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    color: #b3b2ca;
    background: url('./img/thread_background.svg');
}

.message-container {
    display: flex;
    justify-content: flex-start;
    min-height: 50px;
    margin: 10px 15px;
    animation: .65s ease-out 0s show
}

.message-container .time {
    order: 1;
    color: #FFF;
}

.message-container .data {
    order: 2;
    height: 100%;
    margin-left: 25px;
    color: #FFF;
}

.message-container .name {
    font-size: .65em;
    margin-top: 5px;
    text-align: right;
    color: #FFF;
}

.message-container .message {
    background: #DBE0FF;
    border-radius: 5px;
    border-top-left-radius: 0;
    box-sizing: border-box;
    color: #000;
    height: 100%;
    padding: 10px 15px;
    position: relative
}

@keyframes show {
    0% {
        opacity: 0
    }

    100% {
        opacity: 1
    }
}

.message-container .message::before {
    border-bottom-color: transparent;
    border-left-color: transparent;
    border-right-color: #DBE0FF;
    border-style: solid;
    border-top-color: #DBE0FF;
    border-width: 4px;
    content: '';
    height: 0;
    left: -7px;
    position: absolute;
    top: 0;
    width: 0
}

.message-container.right {
    text-align: right;
    justify-content: flex-end
}

.message-container.right .time {
    order: 2;
    margin-left: 25px
}

.message-container.right .data {
    margin-left: 0;
    order: 1
}

.message-container.right .name {
    display: none
}

.message-container.right .message {
    background: #C5C9E5;
    color: #000;
    border-top-right-radius: 0;
    border-top-left-radius: 5px
}

.message-container.right .message::before {
    border-top-color: #C5C9E5;
    border-left-color: #C5C9E5;
    border-right-color: transparent;
    left: auto;
    right: -7px
}

.typing-user {
    text-align: right;
    margin: 10px 15px;
    color: #000;
}

.message-input {
    background: white;
    color: #484d79;
    box-sizing: border-box
}

.message-input .message-form {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-between
}

.message-input .message-form .form-control {
    resize: none;
    padding-left: 15px;
    box-sizing: border-box;
    width: 80%;
    height: 100%;
    border: none;
    font-size: 16px
}

.message-input .message-form .form-control::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #cac8ee
}

.message-input .message-form .form-control::-webkit-scrollbar {
    width: 5px;
    background-color: green
}

.message-input .message-form .form-control::-webkit-scrollbar-thumb {
    background-color: #484d79
}

.message-input .message-form .form-control:focus {
    outline: none
}

.message-input .message-form .send {
    width: 20%;
    box-sizing: border-box;
    font-size: 1.25em;
    text-align: center;
    border: none;
    height: 100%;
    color: #fff;
    background: #6D788B;
    transition: all .35s ease-out
}

.message-input .message-form .send:disabled {
    opacity: .2;
    background: #5d5d8a
}